import styled from 'styled-components'
import logo from '../images/sem-bg-Logo-pagina-home-dhj.png'
import { useState } from 'react'

import CardsHome from '../components/CardsHome'

export default function Home() {
    const [cards] = useState([
        {id: "11", name:"Calculadora", image:"", description:"Vou colocar uma descrição bem grande, só para trapalhar o sistema do mundo doido que eu estou fazendo e nem sei o que mais estou digitando. Ainda cabe, mas vou contrinuar digitando ate estourar", uri:"", type:"project", update:"01/01/2021"},
        {id: "11", name:"Notes", image:"", description:"Testando", uri:"", type:"work", update:"01/01/2021"},
        {id: "11", name:"Learn", image:"", description:"Testando. Este é outra descrição grande para quebrar o componente.", uri:"", type:"contribution", update:"01/01/2021"},
        {id: "11", name:"Titulo bem grande para saber como que vai ficar", image:"", description:"Testando", uri:"", type:"project", update:"01/01/2021"},
        {id: "11", name:"Titulo dois", image:"", description:"Testando", uri:"", type:"work", update:"01/01/2021"},
        {id: "11", name:"Ferramentas online", image:"", description:"Testando", uri:"", type:"project", update:"01/01/2021"},

    ])

    const Container = styled.div`
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: column;
        width: 100vw;
        margin-left: 60px;
    `
    const Area1 = styled.div`
        display: flex;
        flex-direction: row;
        justify-content:center;
        align-items:center;
        width: 80%;
    `
    const Area2 = styled.div`
        display: flex;
        flex-direction: row;
        justify-content:center;
        align-items:center;
        flex-wrap: wrap;
        width: 80%;
        height: inherit;
    `
    const Banner = styled.img`
        width: 300px;
        height: 300px;
    `

    return (
        <Container>
            <Area1>
                <Banner src={logo} />
            </Area1>

            <Area2>
                {cards.map((item, index)=>(
                    <CardsHome 
                        id={item.id}
                        name={item.name}
                        image={item.image}
                        description={item.description}
                        type={item.type}
                        update={item.update}
                        uri={item.uri}
                    />
                ))}
            </Area2>

        </Container>
    )
}