const initialState = {
    id: 'a1',
    name: 'Dhenyson',
    age: 26,
    token: 'asdf',
    theme: 'dark'
}

export default function reducer(state = initialState, action){
    let newState = {...state}
    switch(action.type){
        case 'USER_LOGIN':
            return newState
        default:
            return newState
    }
}