import styled from 'styled-components'
import colors from '../styles/colors'

export default function CardsHome(props) {
    const Container = styled.div`
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;

        background-color: ${colors.background2};

        border-style: solid;
        border-width: 2px;
        border-color: ${colors.background2};

        width: 240px;
        height: 340px;
        padding: 10px;
        margin: 10px;

        transition: transform 1s;

        &:hover{
            transition: border-color 0.8s linear;
            border: solid 2px ${colors.selected};
            cursor: pointer;
            
        }
        font-family: Arial, Helvetica, sans-serif;
    `
    const Header = styled.div`
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 50px;
        background-color: ${colors.background};
    `
    const Name = styled.div`
        padding: 5px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    `
    const Category = styled.div`
        font-size: 10px;
        padding: 5px;
        ${props.type === "project" && `color: ${colors.textSecondary};`}
        ${props.type === "work" && `color: ${colors.textTertiary};`}
        ${props.type === "contribution" && `color: ${colors.textQuaternary};`}
    `
    const Preview = styled.div`
        background-color: ${colors.background};
        width: 100%;
        height: 100px;
        margin-top: 10px;
    `
    const Image = styled.img`
        background-color: ${colors.background};
    `
    // const Description = styled.div``
    
    const Info = styled.div`
        height: 100px;
        display: -webkit-box;
    `
    const TextDescription = styled.div`
        color: ${colors.textOpaque};
        padding: 10px;
        text-align: center;
        display: -webkit-box;
        -webkit-line-clamp: 5;
        -webkit-box-orient: vertical;
        overflow: hidden;
    `
    const LastUpdate = styled.div`
        background-color: ${colors.background};
        display: flex;
        flex-direction: column;
        justify-self: flex-start;
        align-self: flex-end;
        align-items: center;
        width: 100%;
    `
    const Text = styled.div`
        font-size: 12px;
        padding-top: 10px;
    `
    const Date = styled.div`
        padding-bottom: 10px;
    `

    return (
            <Container>
                <Header>                   
                    <Name>
                        {props.name}
                    </Name>

                    <Category>
                        [{props.type}]
                    </Category>
                </Header>

                <Preview>
                    <Image src={"#"}/>
                </Preview>

                      
                    <Info>
                        <TextDescription>
                            {props.description}
                        </TextDescription>
                    </Info>

                    <LastUpdate>
                        <Text>
                            Last update:
                        </Text>
                        <Date>
                           {props.update} 
                        </Date> 
                    </LastUpdate>
              
                
            </Container>
    )
}