import styled from 'styled-components'
import colors from '../../styles/colors'

import { Link } from 'react-router-dom'
import { useSelector, useDispatch } from 'react-redux'

export default function ItemsMenu(props) {
    const selectPage = useSelector(state => state.menuReducer.activePage)
    const dispatch = useDispatch()

    const Items = styled.div`
        width: 50px;
        height: 50px;
        margin-top: 10px;

        background-color: ${colors.background2};
        color: ${selectPage !== props.id ? colors.textPrimary : colors.selected};

        ${selectPage === props.id && `border-left: solid;`}
       
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        &:hover{
            transition: color 0.2s linear;
            transform: scale(1.3);
            color: ${colors.selected};
        }
    `

    const handleClick = (id) => {
        dispatch({ type: 'ACTIVE_PAGE', payload: { activePage: id } })
    }

    return (

        <Link
            to={props.uri}
            onClick={() => handleClick(props.id)}
            style={{ backgroundColor: colors.background2 }}
        >
            <Items>
                {props.children}
            </Items>
        </Link>

    )
}