import styled from 'styled-components'
import colors from '../../styles/colors'
import { useState } from 'react'

//components
import ItemMenuBar from './MenuBarItems'
import ButtonMasterBar from './ButtonMasterBar'
import UserStatus from './UserStatus'
import ChatItem from './ChatButtonBar'

//icones
import HomeIcon from '@material-ui/icons/Home';
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import WorkIcon from '@material-ui/icons/Work';
import MenuBookIcon from '@material-ui/icons/MenuBook';
import SettingsEthernetIcon from '@material-ui/icons/SettingsEthernet';


const ContainerBar = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    position: fixed;

    background-color: ${colors.background2};
    width: 60px;
    height: 100vh;
`
const Area1 = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: ${colors.background2};
`
const Area2 = styled.div`
    background-color: none;
`
const Area1a = styled.div`
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 10px;
    background-color: ${colors.background2};
`

export default function MainContainer(props) {
    const [menu] = useState([
        { title: "HOME", id: "home", uri: "/home", icon:<HomeIcon style={{backgroundColor: colors.background2}}/>},
        { title: "ABOUT", id: "about", uri: "/about", icon:<AssignmentIndIcon style={{backgroundColor: colors.background2}}/> },
        { title: "PORTFOLIO", id: "portfolio", uri: "/portfolio", icon:<WorkIcon style={{backgroundColor: colors.background2}}/> },
        { title: "STUDYS NOTES", id: "notes", uri: "/notes", icon:<MenuBookIcon style={{backgroundColor: colors.background2}}/> },
        { title: "DH PROJECTS", id: "projects", uri: "/projects", icon:<SettingsEthernetIcon style={{backgroundColor: colors.background2}}/> },
    ])

    return (
        <ContainerBar>
            <Area1>
                <Area1a>
                    <ButtonMasterBar />
                    <UserStatus />
                </Area1a>
                

                {menu.map((item, index) => (
                    <ItemMenuBar 
                        title={item.title} 
                        id={item.id} 
                        uri={item.uri}
                        key={index}
                    >
                        {item.icon}
                    </ItemMenuBar>
                ))}
            </Area1>

            <Area2>
                <ChatItem />
            </Area2>

        </ContainerBar>
    )
}